.App {
  text-align: center;
  background-color: #454c58;
  min-height: 100vh;
}

.game-buttons {
  font-size: 64px;
  display: flex;
  justify-content: space-around;
  margin: 5% 25% 10% 25%;
}

.game-buttons p {
  cursor: pointer;
  height: 64px;
  width: 64px;
  padding: 0;
  margin: 0;
}

.selection-display {
  font-size: 32px;
  color: white;
}

#selection {
  color: mediumaquamarine;
}

#computer-selection {
  color: mediumvioletred;
}

.score-display {
  color: gold;
  font-size: 48px;
  line-height: 12px;
  background-color: inherit;
}

.App-header {
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: mediumaquamarine;
}

h1 {
  margin: 0;
}
